import React, { useState } from 'react'
import TextareaAutosize from 'react-textarea-autosize';


const EditAction = ({value, change, changed, changedClass}) => {
  const [editing, setEditing] = useState(false)

  const click = (e) => {
    setEditing(!editing)
  }

  const blur = (e) => {
    setEditing(!editing)
    const data = {
      target: {
        value: e.target.value.replace(/^\s+|\s+$/g, ''),
        id: 'action'
      }
    }
    change(data)
  }

  const focus = (e) => {
    e.target.select()
  }

  const editAction = (e) => {
    change(e)
  }

  const bgClass = changed ? changedClass : ''

  return ( 
    <div className={"mt-2 "}>
      { editing ? 
        <TextareaAutosize onFocus={focus} onBlur={blur} autoFocus id='action' onChange={editAction} className={'focus:outline-none border-2 border-black text-xl font-bold w-11/12 max-w-11/12 '} value={value}></TextareaAutosize>
        :
        value === "" ? 
        <p className="italic text-gray-500 cursor-pointer underline inline-block" onClick={click}>{"Add Action"}</p>
        :
        <p className={"cursor-pointer hover:underline inline-block text-xl font-bold max-w-11/12 " + bgClass} onClick={click}>{value}</p>
      }
    </div>
   );
}
 
export default EditAction;